body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 768px)
  and (-webkit-min-device-pixel-ratio: 2) {
		.logo-nav-bar{
			width: 30px  !important;;
		}
		
}
.navbar{
	padding: 0rem 0rem !important;
}

.logo-nav-bar{
	border-radius : 100%;
	width: 40px;
}
.nav-link{
	width : 100% !important;
	margin-left: 10px;
}

/* Typography */
html {
	font-family: 'Roboto', sans-serif;
}

@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 700px)
  and (-webkit-min-device-pixel-ratio: 2) {
		.img-mobile{
			width : 100%;	
		}
		.img-mobile-trailhead{
			width : 60%;	
		}
		.size-image-full{
			width: 70% !important;
		}
		.img-malt {
			width: 50% !important;
		}
		
		.nav-link{
			margin-left: 5%!important;
		}
	
}
@media (min-width: 576px) {
	html {
		font-size: 14px;
	}
}

@media (min-width: 768px) {
	html {
		font-size: 16px;
	}

}

@media (min-width: 992px) {
	html {
		font-size: 18px;
	}
}

@media (min-width: 1200px) {
	html {
		font-size: 20px;
	}
}

.icons-social i {
	font-size: 3em;
}

/* Custom Styles */



.intro-header-home {
	background: url(/images/hero_bg.jpg) no-repeat center center;
	background-size: cover;
}
/* Block of page */
.block,
.certification-block,
 .main-home  {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	justify-content: center;
	padding: 0 30px;
	text-align: center;
}

 .block > .intro,
 .main-home .intro,
.certification-block > .intro {
	font-family: 'Reem Kufi', sans-serif;
	font-size: 3.0em;
	font-weight: 600;
}

.block > .tagline,
.main-home > .tagline,
.certification-block > .tagline  {
	font-size: 1.5rem;
  /* margin-bottom: 1.5rem; */
	font-weight: 100;
}

.devto {
	margin-bottom: -0.20rem;
}

.devto svg {
	margin-bottom: -0.20rem;
	margin-left: 0.675rem;;
	width: 2.65rem;
	height: 2.65rem;
}
/*  Image About me */

.size-image-full{
	width: 30%;
	border-radius: 100%;
}
.img-malt{
	width : 10%;
}
.certif-img-salesforce{
	border-radius: 3%;
}
@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 700px)
  and (-webkit-min-device-pixel-ratio: 2) {
	
    .intro-divider {
        width: 80% !important;
    }
}

.sub-title-home{
    font-weight: 300;
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
  }
  .sub-title-home-first{
    font-weight: 400;
    font-size:1.7rem;
  }
  .icons-social i {
      padding: 10px;
  }
  .intro-divider {
    width: 400px;
    border-top: 1px solid #f8f8f8;
	border-bottom: 1px solid rgba(0,0,0,0.2);
	margin-top: 0;
}

/*Content Panel*/
.content-panel {
    background: #ffffff;
    box-shadow: 0px 3px 2px #aab2bd;
    padding-top: 25px;
    padding-bottom: 5px;
    margin-top: 15px;
    border-radius: 5% !important;
}
.content-panel h4 {
    margin-left: 10px;
  
}
/*Panel Size*/

.pn {
    height: 270px;
    box-shadow: 0 2px 1px rgba(0, 0, 0, 0.2);
}

.pn:hover {
    box-shadow: 2px 3px 2px rgba(0, 0, 0, 0.3);
}
.img-project{
    width: 50%;
    margin: auto;
    padding-top: 4%;
}
#blog-bg {
    /*background: url(../images/Salesforce_Logo.png) no-repeat center top;*/
    margin-top: -15px;
    background-attachment: relative;
    background-position: center center;
    min-height: 150px;
    width: 100%;
    background-size: 100%;
    background-size: cover;
}
.blog-text {
    margin-left: 8px;
    margin-right: 8px;
    margin-top: 15px;
    font-size: 12px;
}

.blog-text > .title-project {
    margin-bottom : 0px;
}
.modal-title-project{
    color :#45C1E1;
}
.img-project{
    width: 50%;
    margin: auto;
    padding-top: 4%;
}
.btn-primary{
    background-color: rgb(41, 149, 212) !important;
    border-color: rgb(41, 149, 212) !important;
}
.title-project{
    min-height: 36px;
}
.modal{
    text-align: left;
    font-size: medium;
    font-size: initial;
}
.modal-header{
    padding-bottom : 0px !important;
}
.title-modal{
    color: rgb(41, 149, 212);
    font-weight: 700;
}
.modal-title > p, 
.modal-body > p  {
    margin-top: 0;
    margin-bottom: 0rem !important;
}
.btn-secondary {
    color: #fff;
    background-color: rgb(41, 149, 212) !important;
    border-color: rgb(41, 149, 212) !important;
}
/* Theme */

.navbar{
	background-color: rgb(41, 149, 212) !important;
}


.main-home,
.navbar-light .navbar-brand,
.navbar-light .navbar-nav .nav-link,
.navbar-light .navbar-nav .active>.nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show>.nav-link{
	color: white !important;
	/* color: rgb(41, 149, 212); */

}

/* home title change color hover text */
.title-block{
    color:rgb(41, 149, 212) !important;	
}

.first {
	background: #f7faff;
	color: rgb(41, 149, 212);
}
.second{
	background:  rgb(41, 149, 212);
	color: #f7faff;
}
.icons-social a {
	color: white;
}

.icons-social a #f7faff{
	/* fill: rgb(41, 149, 212); */
	fill: white;
}
.certification-block > a,
.certification-block > a:link,
.certification-block > a:visited {
	color:white !important;
}

.text-color-white{
	color:white;
}
.text-color-blue,
.icons-social a:hover{
	color:rgb(41, 149, 212);
}
